.estimate aside {
  width: 72px;
  --offset: -72px;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 0;
  background-color: #222;
  color: #fff;
  position: relative;
  margin: 0;
}

.estimate aside .side-button,
.estimate aside .sidebar-bottom {
  width: 100%;
  text-align: center;
  padding-bottom: 4px;
}

.estimate aside .side-button button {
  width: 38px;
  padding-left: initial;
  padding-right: initial;
}

.estimate aside .side-button::after {
  content: attr(data-title);
  display: block;
  position: relative;
  font-size: 10px;
  -webkit-transition: -webkit-transform .4s 1s;
  transition: -webkit-transform .4s 1s;
  -o-transition: transform .4s 1s;
  transition: transform .4s 1s;
  transition: transform .4s 1s, -webkit-transform .4s 1s;
  font-variant: all-petite-caps;
}

.estimate aside .sidebar-bottom {
  position: absolute;
  bottom: 6px;
}

.estimate aside .side-button:nth-child(1)::after { -webkit-transform: translate(calc(1 * var(--offset))); -ms-transform: translate(calc(1 * var(--offset))); transform: translate(calc(1 * var(--offset))); }
.estimate aside .side-button:nth-child(2)::after { -webkit-transform: translate(calc(2 * var(--offset))); -ms-transform: translate(calc(2 * var(--offset))); transform: translate(calc(2 * var(--offset))); }
.estimate aside .side-button:nth-child(3)::after { -webkit-transform: translate(calc(3 * var(--offset))); -ms-transform: translate(calc(3 * var(--offset))); transform: translate(calc(3 * var(--offset))); }
.estimate aside .side-button:nth-child(4)::after { -webkit-transform: translate(calc(4 * var(--offset))); -ms-transform: translate(calc(4 * var(--offset))); transform: translate(calc(4 * var(--offset))); }
.estimate aside .side-button:nth-child(5)::after { -webkit-transform: translate(calc(5 * var(--offset))); -ms-transform: translate(calc(5 * var(--offset))); transform: translate(calc(5 * var(--offset))); }
.estimate aside .side-button:nth-child(6)::after { -webkit-transform: translate(calc(6 * var(--offset))); -ms-transform: translate(calc(6 * var(--offset))); transform: translate(calc(6 * var(--offset))); }
.estimate aside .side-button:nth-child(7)::after { -webkit-transform: translate(calc(7 * var(--offset))); -ms-transform: translate(calc(7 * var(--offset))); transform: translate(calc(7 * var(--offset))); }
.estimate aside .side-button:nth-child(8)::after { -webkit-transform: translate(calc(8 * var(--offset))); -ms-transform: translate(calc(8 * var(--offset))); transform: translate(calc(8 * var(--offset))); }
.estimate aside .side-button:nth-child(9)::after { -webkit-transform: translate(calc(9 * var(--offset))); -ms-transform: translate(calc(9 * var(--offset))); transform: translate(calc(9 * var(--offset))); }

.estimate aside:hover .side-button::after {
  -webkit-transform: translate(0);
      -ms-transform: translate(0);
          transform: translate(0);
  -webkit-transition: -webkit-transform .4s;
  transition: -webkit-transform .4s;
  -o-transition: transform .4s;
  transition: transform .4s;
  transition: transform .4s, -webkit-transform .4s;
}

.side-button[data-color='primary'] { color: #007bff; }
.side-button[data-color='danger'] { color: #dc3545; }
.side-button[data-color='secondary'] { color: #6c757d; }
.side-button[data-color='warning'] { color: #ffc107; }
.side-button[data-color='success'] { color: #28a745; }



